.text_field {
  display: block;
  width: 100%;
  height: calc(2.25rem + 2px);
  padding: 0.375rem 0.75rem;
  font-family: inherit;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #161b22;
  background-color: #fff;
  border: 1px solid #bdbdbd;
  border-radius: .7rem;
}

